.navbar {
    &__wrapper {
        position: relative;
        @include flexBoxLayout($alignItems: center);
    }

    &__left {
        @include flexBox($justifyContent: center, $alignItems: center);
        min-width: 100%;

        box-shadow: 0 5px 6px 0 #00000014;

        @media (min-width: $breakpoint-tablet) {
            min-width: inherit;
            box-shadow: none;
        }
    }

    &__menu {
        &-toggle {
            position: absolute;
            left: 16px;
            height: 30px;
            vertical-align: middle;
            width: 30px;
        }

        &-icon {
            background: $primary-color none repeat scroll 0 0;
            bottom: 0;
            display: block;
            height: 3px;
            left: 0;
            margin: auto;
            position: absolute;
            right: 0;
            top: 0;
            transition: all 0.3s ease 0s;
            width: 100%;
            border-radius: 3px;

            &::before,
            &::after {
                background-color: $primary-color;
                content: '';
                height: 3px;
                left: 0;
                margin: auto;
                position: absolute;
                right: 0;
                transition: all 0.3s ease 0s;
                width: 100%;
                border-radius: 3px;
            }

            &::before {
                bottom: 8px;
            }
            &::after {
                bottom: -8px;
            }
        }
    }

    &__logo {
        flex-basis: auto;
    }

    &__actions {
        @include flexBox($alignItems: center);
        flex: 1;
        // min-width: 100%;
        padding: $padding-small;

        @media (min-width: $breakpoint-tablet) {
            // min-width: initial;
            justify-content: flex-end;
            padding: $padding-small;
        }

        & > * {
            flex: 1;
            margin-left: 16px;

            @media (min-width: $breakpoint-tablet) {
                flex-basis: auto;
                flex-grow: 0;
                flex-shrink: 0;
            }
        }

        & :first-child {
            margin-left: 0;
        }

        & :last-child {
            margin-right: 0;
        }
    }
}
