.card {
    background-color: #fff;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);

    &__header {
        overflow: hidden;
    }

    &__image {
    }
    &__content {
        padding: 16px 16px 24px;
    }

    &__category {
        color: $secondary-color;
        font-size: 16px;
    }

    &__heading {
        color: $primary-color;
        font-size: 22px;
        font-weight: bold;
    }
}
