.form {
    max-width: 320px;
    &__heading {
        font-size: 32px;
        font-weight: bold;
    }

    &__row {
        @include flexBox($flexDirection: row, $alignItems: center);
        margin-bottom: 16px;
    }

    &__label {
        flex: 1;
        display: block;
        font-size: 14px;
        color: #000;
        margin-top: $padding-small;
        margin-bottom: $padding-extra-small;

        &--bold {
            font-weight: bold;
        }

        &--gap {
            margin-top: $padding-large $padding-extra-small;
        }

        &--inline {
            display: inline-block;
            padding-left: $padding-extra-small;
            margin-top: 0;
            margin-bottom: 0;
        }

        &--hidden {
            display: none;
        }
    }

    &__input {
        display: block;
        width: 100%;
        background-color: $primary-ligth-color;
        padding: $padding-small;
        border: 1px solid $primary-ligth-color;
    }

    &__submit {
        color: $primary-ligth-color;
        font-weight: bold;
        padding: 13px 77px 12px 66px;
        width: 100%;
        background-color: $action-wp-color;
        border: $action-wp-color;
    }
}
