.close {
    position: relative;
    width: 100%;
    padding: $padding-small;

    &__icon {
        @include flexBox($justifyContent: flex-end, $alignItems: center);
        font-size: 22px;
        line-height: 22px;
        opacity: 0.8;

        &:hover {
            opacity: 1;
        }
    }
}
