.feature {
    &__wrapper {
        @include flexBox;
    }
    &__media {
        flex-basis: auto;
    }
    &__icon {
        max-width: 36px;
    }

    &__content {
        @include flexBox($flexDirection: column);
        padding-left: $padding-large;
    }
    &__heading {
        font-size: 22px;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 16px;
    }
    &__detail {
        margin: 0;
    }
}