@import './partials/boilerplate';
@import './partials/utils';
@import './partials/shared';

// components
@import './partials/close-button';
@import './partials/header';
@import './partials/navbar';
@import './partials/menu';
@import './partials/form';
@import './partials/card';
@import './partials/info-item';
@import './partials/banner';
@import './partials/feature';
@import './partials/belt';
@import './partials/footer';
@import './partials/embed';

.logo {
    &--menu-fixed {
        padding-left: $padding-small;
        @include flexBox($alignItems: center);

        .logo__image {
            max-width: 64px;
        }
    }

    &__redirect {
        display: contents;
        text-decoration: none;
    }

    &__image {
        max-width: 132px;

        @media (min-width: $breakpoint-tablet) {
            max-width: 244px;
        }
    }
}

.button {
    color: $primary-ligth-color;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    background-color: $primary-color;
    border-width: 1px;
    padding: $padding-small;
    border: 1px solid $primary-color;

    &--outline {
        color: $primary-color;
        background-color: $primary-ligth-color;
        &-whatsapp {
            color: $action-wp-color;
            background-color: $action-wp-color;
        }
    }

    &--whatsapp {
        background-color: $action-wp-color;
        border-color: $action-wp-color;
    }
}

.home {
    // background-color: cyan;
}

.section {
    padding-top: $padding-large;
    padding-bottom: $padding-large;

    &--extra-gap {
        padding-top: $padding-extra-large;
        padding-bottom: $padding-extra-large;
    }

    &--no-gap {
        padding-top: 0;
        padding-bottom: 0;
    }

    &--striped {
        background-color: $accent-color-ligth;
    }

    &__wrapper {
        &--container {
            max-width: 1240px;
            padding-left: $padding-small;
            padding-right: $padding-small;
            margin: 0 auto;
            @media (min-width: $breakpoint-tablet) {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    &__header {
        text-align: center;
        margin-bottom: 2rem;
    }

    &__heading {
        font-size: 36px;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 1rem;
    }

    &__subheading {
        color: $primary-color;
        font-size: 22px;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 0.5rem;
    }

    &__content {
        &--header {
            @include flexBoxLayout;
        }

        &--curses {
            @include sectionGrid;
        }

        &--experience {
            @include flexBoxLayout($alignItems: center);
        }

        &--info {
            @include flexBoxLayout($alignItems: flex-start, $justifyContent: center);

            & > * {
                padding-top: $padding-large;
                padding-bottom: $padding-large;

                &:first-child {
                    padding-top: 0;
                }
                &:last-child {
                    padding-bottom: 0;
                }

                @media (min-width: $breakpoint-tablet) {
                    padding-top: 0;
                    padding-bottom: 0;
                }
            }
        }

        &--features {
            @include sectionGrid($min: 360px);
        }
    }
}

.column {
    flex: 1;
    padding-top: $padding-small;
    padding-bottom: $padding-small;

    &:first-child {
        padding-top: 0;
    }
    &:last-child {
        padding-bottom: 0;
    }

    @media (min-width: $breakpoint-tablet) {
        padding: 0 $padding-medium;
        &:first-child {
            padding-left: 0;
        }
        &:last-child {
            padding-right: 0;
        }
    }

    &--wide {
        flex-grow: 1.6;
    }

    &--centered-content {
        @include flexBox($justifyContent: center);
    }
}

.autorization {
    &__wrapper {
        @include flexBox($justifyContent: center);
    }
    &__media {
        max-width: 836px;
    }

    &__image {
    }
}

.float{
	position:fixed;
	width:60px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:#25d366;
	color:#FFF;
	border-radius:50px;
	text-align:center;
  font-size:30px;
	box-shadow: 2px 2px 3px #999;
  z-index:100;
}

.my-float{
	margin-top:16px;
}
