.belt {
    background-color: $action-wp-color;
    padding: $padding-medium;

    &__wrapper {
        // flex: 1;
        @include flexBoxLayout($alignItems: center, $justifyContent: center);
        @media (min-width: $breakpoint-tablet) {
            justify-content: space-between;
        }
    }

    &__logo {
        @include flexBox($alignItems: center, $justifyContent: center);

        @media (min-width: $breakpoint-tablet) {
            justify-content: flex-start;
            width: 56px;
        }
    }

    &__image {
        width: 100%;
        max-width: 56px;
    }

    &__label,
    &__number {
        text-align: center;
        font-weight: bold;
        margin: 8px 0;
    }

    &__label {
        font-size: 20px;
        line-height: 24px;

        @media (min-width: $breakpoint-tablet) {
            font-size: 40px;
            padding-left: 48px;
        }
    }

    &__number {
        font-size: 32px;
        line-height: 36px;

        @media (min-width: $breakpoint-tablet) {
            font-size: 40px;
            padding-left: 96px;
        }
    }
}
