.embed {
    position: relative;
    &__wrapper {
        position: relative;
        display: block;
        width: 100%;
        padding: 0;
        overflow: hidden;

        &::before {
            display: block;
            content: '';
        }

        &--16by9 {
            &::before {
                padding-top: 56.25%;
            }
        }

        &--4by3 {
            &::before {
                padding-top: 75%;
            }
        }

        &--3by2 {
            &::before {
                padding-top: 66.66%;
            }
        }
    }

    &__element {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 0;
    }
}
