.footer {
    background-color: $accent-color;
    color: $primary-ligth-color;

    &__top {
    }

    &__main {
        @include flexBoxLayout;
        padding-top: $padding-large;
        padding-bottom: $padding-large;
    }

    &__bottom {
        @include flexBoxLayout;

        padding-top: $padding-small;
        padding-bottom: $padding-large;

        @media (min-width: $breakpoint-tablet) {
            padding-top: $padding-large;
            padding-bottom: $padding-large;
        }
    }

    &__column {
        flex: 1;

        padding-top: $padding-small;
        padding-bottom: $padding-small;

        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            padding-bottom: 0;
        }

        @media (min-width: $breakpoint-tablet) {
            display: inline-block;
        }

        &--wide {
            flex-grow: 2;
        }
    }

    &__list {
        @include listStyle;
    }

    &__item {
        font-size: 14px;
        line-height: 20px;

        padding-top: $padding-extra-small;
        padding-bottom: $padding-extra-small;

        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            padding-bottom: 0;
        }

        &--social {
            display: inline-block;

            padding-left: $padding-extra-small;
            padding-right: $padding-extra-small;

            &:first-child {
                padding-left: 0;
            }
            &:last-child {
                padding-right: 0;
            }
        }

        &--legal {
            @media (min-width: $breakpoint-tablet) {
                display: inline-block;

                padding-left: $padding-extra-small;
                padding-right: $padding-extra-small;

                &:first-child {
                    padding-left: 0;
                }
                &:last-child {
                    padding-right: 0;
                }
            }
        }
    }

    &__heading {
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 16px;
    }

    &__link {
        color: inherit;
        text-decoration: none;
    }

    &__icon {
        max-height: 32px;
    }

    &__logo {
        max-width: 96px;
        margin-bottom: 32px;
    }
}
