@import './partials/shared';
.modifier--container {
    max-width: 1240px;
    padding-left: $padding-small;
    padding-right: $padding-small;
    margin: 0 auto;
    @media (min-width: $breakpoint-tablet) {
        padding-left: 0;
        padding-right: 0;
    }
}

.hidden {
    &--mobile {
        @media (max-width: $breakpoint-tablet) {
            display: none;
        }
    }

    &--desktop {
        @media (min-width: $breakpoint-tablet) {
            display: none;
        }
    }
}

.heading-color {
    color: $secondary-color;
}
