.menu {
    @include flexBox($flexDirection: column);
    position: fixed;
    top: 0;
    left: -200px;
    height: 100vh;
    width: 200px;
    transition: all 0.5s ease;
    background-color: $primary-ligth-color;
    z-index: 9;

    @media (min-width: $breakpoint-tablet) {
        flex: 1;
        @include flexBox($alignItems: center);
        position: sticky;
        left: initial;
        width: initial;
        height: initial;
        box-shadow: 0 5px 6px 0 #00000014;
    }

    &--open {
        left: 0px;
        box-shadow: 2px 2px 2px 0px #00000032;

        @media (min-width: $breakpoint-tablet) {
            box-shadow: none;
        }
    }

    &__header {
        flex-basis: auto;
    }

    &__list {
        @include listStyle;
        flex: 1;

        &--sub {
            display: none;
        }
    }

    &__item {
        padding: $padding-small;
        border-bottom: 1px dashed $primary-color;

        &--active {
            color: $primary-ligth-color;
            background-color: $secondary-color;
        }

        @media (min-width: $breakpoint-tablet) {
            display: inline-block;
            border-bottom: none;
        }
    }

    &__link {
        color: inherit;
        text-transform: uppercase;
        text-decoration: none;
    }
}
