$primary-color: #000;
$primary-ligth-color: #fff;
$secondary-color: #f02123;
$accent-color: #1e272e;
$accent-color-ligth: #e8e8e8;
$action-wp-color: #4caf50;
$breakpoint-tablet: 768px;
$padding-extra-small: 8px;
$padding-small: 16px;
$padding-medium: 24px;
$padding-large: 32px;
$padding-extra-large: 64px;

@mixin flexBoxLayout($display: flex, $justifyContent: stretch, $alignItems: stretch) {
    flex: 1;
    display: $display;
    flex-direction: column;
    justify-content: $justifyContent;

    @media (min-width: $breakpoint-tablet) {
        flex-direction: row;
        align-items: $alignItems;
    }
}

@mixin flexBox($display: flex, $flexDirection: row, $justifyContent: stretch, $alignItems: stretch) {
    display: $display;
    flex-direction: $flexDirection;
    justify-content: $justifyContent;
    align-items: $alignItems;
}

@mixin sectionGrid($min: 320px) {
    display: grid;
    // column-gap: 16px;
    // row-gap: 24px;
    gap: 32px;
    grid-auto-flow: dense;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(auto-fill, minmax($min, 1fr));
}

@mixin sectionColumns($equal: true) {
    @include flexBoxLayout;

    & * > {
        flex: 1;
    }
}

@mixin listStyle {
    list-style: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}
